.App {
  text-align: center;
}

body{
  font-family: sans-serif;
  color: #fff;
  background-color:#272829;
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  left: 0;
  right: 0;
}
.navBar{
  display: flex;
  padding: .75rem;
  flex-direction: column;
  background: #FFF6E0;
  font-weight: bold;
    .bar{
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        & li :hover{
          color: #1068d1;
          border-bottom: 3px solid #000;
          transition: all .2s ease-in;
        }
       
    }
     
}
.Path{
  padding: 1rem;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  margin-top: 2rem !important;
}
li{
  text-decoration: none;
  list-style: none;
}
h2{
  padding: 1rem;
}
.examLt{
  width: auto;
  margin: 0 auto;
  text-align: left;

  padding: 2rem;
}
.examLt li{
  background: #fff;
  border-radius: 4px;
  margin-bottom: 1rem;
  padding: .5rem;
  & a{
  font-size: 15px;
  font-weight: 700;
  text-transform: uppercase;
  }
  
}


.examLt li:hover{
  background: linear-gradient(5deg, #207393, transparent);
}

.box{
  width: 100vw;
  text-align: center;
  /* background: radial-gradient(#e3e3e3, transparent); */
  border: 2px solid #FFF6E0;
  height: auto;
  padding: 1rem;
  border-radius:12px;
}
a{
  text-decoration: none;
  color: #272829;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.addButton{
  padding: .5rem;
  border: 3px solid #FFF6E0;
  border-radius: 8px;
  cursor: pointer;
  color: #fff;
  background-color:#272829;
  width: 150px;
}
.addButton:hover{
  background-color: #FFF6E0;
  color: #282c34;
}
.fstyle{
  gap: 1rem;
  display: flex;
  flex-direction: initial;
  justify-content: space-between;
  align-items: stretch;
}
.d-flexC{
  flex-direction: column;
  display: flex;
}
.d-flexR{
  flex-direction: row;
  display: flex;
}
.justify-center{
  justify-content: center;
}
.justify-even{
  justify-content: space-evenly;
}
.align-center{
  align-items: center;
}
.align-start{
  align-items: flex-start;
}

.justify-between{
  justify-content: space-between;
}

.justify-around{
  justify-content: space-around;
}
.w-auto{
  width: auto;
}
.w-100{
  width: 100%;
}
.mx-auto{
  margin: 0 auto;
}
.p-2{
  padding: 2rem;
}
.p-1{
  padding: 1rem;
}
.text-left{
  text-align: left;
}
.text-right{
  text-align: right;
}

.text-center{
  text-align: center;
}
.gap{
  gap:.5rem;
}
.gap2{
  gap:2rem;
}
input{
  border-radius: 5px;
  padding: .75rem;
  border:2px solid #180303;
}
textarea{
  border-radius: 5px;
  padding: .75rem;
  border:2px solid #180303;
}
input:focus{
  border:2px solid #1068d1;
}
label{
  font-size: 15px;
  font-weight: 600;
  color: #FFF6E0;
  padding: .5rem;
}
.f-wrap{
flex-wrap: wrap;

}
.answerBox{
  border: 2px solid #000;
  padding: .5rem;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: space-around;
  gap: .5rem;
  border-radius: 5px;
  width: auto;
  box-shadow: 1px 1px 5px 4px #cbc7c7;
}
select{
  border: 2px solid #000;
    border-radius: 5px;
    padding: .5rem;
    cursor: pointer;
}
table{
  border: 2px solid #000;
  padding: 1rem;
}
td,th{
  padding: 1rem;
  background-origin: padding-box;
  border: 1px solid #000;
}
input[type=radio]
{cursor: pointer;}

.viewAnswer{
  color: #fff;
    text-align: center;
    font-weight: 800;
}
.viewAnswer:hover{
color: #97c0ff;
}

/* 50% width for large desktops (min-width: 1200px) */
@media (min-width: 1200px) {
  .Path {
    width: 50%;
  }
}

/* Additional media queries for other screen sizes */
@media (max-width: 1199px) {
  /* Custom styles for smaller desktops and laptops (up to 1199px) */
  .Path {
    width: 70%;
  }
}

@media (max-width: 991px) {
  /* Custom styles for tablets and larger mobile devices (up to 991px) */
  .Path {
    width: 80%;
  }
}

@media (max-width: 767px) {
  /* Custom styles for smaller tablets and mobile devices (up to 767px) */
  .Path {
    width: 100%;
  }
}

/* Add more media queries as needed for other screen sizes */